<script setup>
import 'aplayer/dist/APlayer.min.css';
import APlayer from 'aplayer';
import { onMounted, ref } from 'vue';
import  {NFloatButton ,NIcon,NModal,NCard,
  NP,NAlert,NLog,NInput,NInputGroup,NButton
} from "naive-ui"
import {SettingsOutline} from "@vicons/ionicons5"

// const IndexFileURL=
// location.host=="localhost:5173"
// ?"http://localhost:8000/files/index.json"
// :"/files/index.json"

const host=ref("localhost:5173"
?"http://localhost:8000"
:""
)

const showSettingsModal=ref(false)

const logMessage=ref(""),logStatus=ref("")

onMounted(()=>{
  initAPlayer()
})

let SongInfo=[]

const initAPlayer=()=>{
  fetch(host.value+"/files/index.json").then(v=>v.json()).then(op=>{
    // console.log("success");
    SongInfo=op
    logStatus.value="success"
    const ap = new APlayer({
      loop: 'one',
    order: 'random',
    mutex: true,
    listFolded: false,
    mini: false,
    autoplay: false,
    lrcType: 3,
  container: document.getElementById('aplayer'),
    audio: SongInfo
});
  }).catch(e=>{
    console.log("fail");
    showSettingsModal.value=true
    logMessage.value=e.toString()
    logStatus.value="error"
    
  })
}

const handleVerifyAndSave=()=>{
  fetch(host.value+"/files/index.json").then(v=>v.json()).then(op=>{
    logStatus.value="success"
    SongInfo=op
    localStorage.setItem("__5dbwat4_proj__music_player__version","0.1")
    localStorage.setItem("__5dbwat4_proj__music_player__hostInfo",host.value)
  }).catch(e=>{
    console.log("fail");
    showSettingsModal.value=true
    logMessage.value=e.toString()
    logStatus.value="error"
    
  })
}

// const DownLinkS=ref(navigator.connection.downlink)
// navigator.connection.addEventListener('change', ()=>{
//   DownLinkS.value=navigator.connection.downlink
// });
</script>

<template>
    <n-float-button :right="15" :top="15" @click="showSettingsModal=true">
      <n-icon>
        <SettingsOutline />
      </n-icon>
    </n-float-button>
    <n-modal v-model:show="showSettingsModal">
    <n-card
      style="width: 70%"
      title="Settings"
      :bordered="false"
      size="huge"
      role="dialog"
      aria-modal="true"
    >
    <n-alert title="" type="success" v-if="logStatus=='success'">
      OK.
    </n-alert>
    <n-alert title="" type="error" v-if="logStatus=='error'">
      <n-log :log="logMessage" :rows="3"></n-log>
    </n-alert>
    <n-p>API host.</n-p>
    <n-input-group>
    <n-input v-model:value="host"></n-input>
    <n-button @click="handleVerifyAndSave">Verify and save</n-button>
  </n-input-group>
    </n-card>
  </n-modal>
    <div id="landing">
      <div id="aplayer-wrapper">
        <div id="aplayer"></div>

      </div>
    </div>
</template>

